import { useState } from "react";
import Select from "react-select";
import { ChevronCollapse, Filtered } from "../../svg-icons";
import {
  Filter,
  GreyBorderBottomTitle,
  LegendLabel,
  OpenCloseButton,
  StyledFilterBoxes,
  StyledFilterPanel,
  StyledFilterTogglePane,
} from "./styles";

interface FilterPanelProps {
  updateParent: any;
  currentFilterObject: any;
}

const FilterPanel: React.FC<FilterPanelProps> = (props) => {
  const { updateParent, currentFilterObject } = props;
  const [isExpanded, setIsExpanded] = useState(false);

  const handleToggle = () => {
    setIsExpanded(!isExpanded);
  };

  const sizeOptions = [
    { value: "Not Specified", label: "Not Specified" },
    { value: "Fist", label: "Fist" },
    { value: "Shoe Box", label: "Shoe Box" },
    { value: "Coffee Table", label: "Coffee Table" },
    { value: "Car", label: "Car" },
    { value: "Bus", label: "Bus" },
    { value: "Playground", label: "Playground" },
    { value: "Sports Field", label: "Sports Field" },
    { value: "Other", label: "Other" },
  ];

  const typeOptions = [
    { value: "Sighting", label: "Sighting" },
    { value: "Sample", label: "Sample" },
    { value: null, label: "All" },
  ];

  const colorOptions = [
    { value: "Red", label: "Red" },
    { value: "Pink", label: "Pink" },
    { value: "Grey", label: "Grey" },
    { value: "Green", label: "Green" },
    { value: "Yellow", label: "Yellow" },
    { value: "Other", label: "Other" },
    { value: null, label: "All" },
  ];

  const defaults = {
    sizes: null,
    color: colorOptions[6],
    type: typeOptions[2],
  };
  const [defaultValues, setDefaultValues] = useState(defaults);

  // cannot get TS annotations working on this even though @types/react-select is a dep
  const handleSizesFilterUpdate = (selection: any) => {
    updateParent({
      ...currentFilterObject,
      sizes:
        selection.length > 0 ? selection.map((item: any) => item.value) : null,
    });
    setDefaultValues({
      ...defaultValues,
      sizes: selection.length > 0 ? selection : null,
    });
  };
  const handleTypeFilterUpdate = (selection: any) => {
    updateParent({
      ...currentFilterObject,
      type: selection.value,
    });
    setDefaultValues({
      ...defaultValues,
      type: selection,
    });
  };

  const handleColorFilterUpdate = (selection: any) => {
    updateParent({
      ...currentFilterObject,
      color: selection.value,
    });
    setDefaultValues({
      ...defaultValues,
      color: selection,
    });
  };

  const defaultCheck = () => {
    return (
      defaultValues.color.value === null &&
      defaultValues.color.label === "All" &&
      defaultValues.type.value === null &&
      defaultValues.type.label === "All" &&
      defaultValues.sizes === null
    );
  };

  return (
    <StyledFilterPanel open={isExpanded} className="filter-panel">
      <StyledFilterTogglePane className="toggle-pane">
        {isExpanded && (
          <>
            <GreyBorderBottomTitle margin_bottom="10px" font_size="1.3rem">
              LSP Demo Simple Filter
            </GreyBorderBottomTitle>
            <StyledFilterBoxes>
              <LegendLabel> Size (Multi-select) </LegendLabel>
              <Select
                // key={sizeOptions} // using key to force update https://github.com/facebook/react/issues/4101#issuecomment-243625941
                options={sizeOptions}
                isSearchable={false}
                isMulti
                onChange={handleSizesFilterUpdate}
                defaultValue={defaultValues.sizes}
                maxMenuHeight={150}
              />
            </StyledFilterBoxes>
            <StyledFilterBoxes>
              <LegendLabel> Record Type</LegendLabel>
              <Select
                options={typeOptions}
                isSearchable={false}
                onChange={handleTypeFilterUpdate}
                defaultValue={defaultValues.type}
              />
            </StyledFilterBoxes>
            <StyledFilterBoxes>
              <LegendLabel> Color</LegendLabel>
              <Select
                options={colorOptions}
                isSearchable={false}
                onChange={handleColorFilterUpdate}
                defaultValue={defaultValues.color}
                maxMenuHeight={100}
              />
            </StyledFilterBoxes>
          </>
        )}
      </StyledFilterTogglePane>
      <OpenCloseButton
        onClick={handleToggle}
        title={isExpanded ? "collapse panel" : "expand panel"}
      >
        {!isExpanded && (
          <p style={{ fontSize: "1.2rem", margin: "-6px 5px 5px 0px" }}>
            Filter Map
          </p>
        )}
        {isExpanded
          ? ChevronCollapse
          : defaultCheck()
          ? Filter({ height: 24, width: 24 })
          : Filtered({ height: 24, width: 24 })}
      </OpenCloseButton>
    </StyledFilterPanel>
  );
};

export default FilterPanel;
