import { colors } from "react-select/dist/declarations/src/theme";

/**
 * Generates a properly formatted filter object to pass to a MapGL <Layer/> component
 *
 * @returns {any[]} the formatted filter list ready to be passed to a MapGL <Layer/> component
 */
export function recordsFilterCompositor(filterObject: RecordsFilterObject) {
  const { color, sizes, type, users, orgs } = filterObject;

  let filter: any[] = ["all"];
  if (color) {
    filter.push(["in", color, ["get", "colors"]]);
  }
  if (sizes) {
    filter.push(["match", ["get", "size"], sizes, true, false]);
  }
  if (type) {
    filter.push(["match", ["get", "type"], type, true, false]);
  }
  if (users) {
    filter.push(["match", ["get", "name"], users, true, false]);
  }
  if (orgs) {
    filter.push(["match", ["get", "organization"], orgs, true, false]);
  }

  return filter;
}

interface RecordsFilterObject {
  color?: string[];
  sizes?: string[];
  type?: string;
  users?: string[];
  orgs?: string[];
}
